.backdrop {

    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 20;
    background-color: rgba(0, 0, 0, 0.75);
  }
  
 
  .modal {
    position: fixed;   
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
    z-index: 101;
    
    animation: slide-down 300ms ease-out forwards;
  }
  


