@import url(https://fonts.googleapis.com/css2?family=Libre+Baskerville:wght@700&display=swap);

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{

  font-family: Libre Baskerville;
  overflow: hidden;
}

.gallery{
  -webkit-column-count: 4;
          column-count: 4;
  grid-column-gap: 1em;
  -webkit-column-gap: 1em;
          column-gap: 1em;
  margin: 1em;
}
.tile{
  position: relative;
  background-color: #eee;
  display: inline-block;
  margin: 0 0 1em;
  width: 100%;
}

.item{
  position: relative;

}

.name{
  position: absolute;
  bottom: 8px;
  left: 16px;
  color: white;
}

.header{
  width: 100%;
}



.main-content {
    height: 70vh;
    display: flex;
    align-items: center;
    margin-top: 50px;
    margin-left: 100px;
    overflow: hidden;
    font-family: Libre Baskerville;
}

.title {
    padding-left: 50px;
    position: absolute;
    top: 18%;
    left: 40%;
    -webkit-transform: translate(-40%);
            transform: translate(-40%);
    background-color: white;
    width: 300px;
    height: 240px;
    z-index: 100;
    line-height: 64px;
}

.title p {
    color: #7D7D7D;
    margin: 0;
    padding: 0;
    font-family: Libre Baskerville;
    font-style: normal;
    font-weight: normal;
    font-size: 15px;
    line-height: 19px;
}

.title h2 {
    font-size: 50px;
    margin: 0;
    padding: 0;
    font-family: Libre Baskerville;
}

.info {
    height: 100%;
    margin-left: 200px;
    color: #7D7D7D;
}

div.info p {
    max-width: 350px;
    max-height: 360px;
    overflow: hidden;
    position: relative;
    margin-top: 150px;
    z-index: 100;
}

div.info h2 {
    position: absolute;
    top: 0;
    color: #F3F3F3;
    font-size: 200px;
}

.artist {
    align-self: flex-end;
    margin-left: 30px;
}

.footer {
    font-family: Libre Baskerville;
    position: fixed;
    padding: 10px;
    left: 0;
    bottom: 0;
    width: 100%;
    border-top: 1px solid black;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

div.footer h4 {
    font-size: 18px;
    line-height: 22px;
    color: #000000;
}

div.footer p {
    font-size: 13px;
    line-height: 16px;
    color: #000000;
    mix-blend-mode: normal;
    opacity: 0.75;
}

div.footer h4, div.footer p {
    padding: 0;
    margin: 5px;
    margin-left: 30px;
}

.nav {
    margin-right: 2em;
}

div.nav img {
    margin-left: 10px;
}

button {
    cursor: pointer;
    border: none;
    outline: none;
    background-color: transparent;
}

.art {
    height: 560px;
}

.main-image {
    position: relative;
}

.max-image {
    display: flex;
    align-items: center;
    position: absolute;
    left: 10px;
    bottom: 10px;
    color: white;
    background-color: white;
    background: rgba(255, 255, 255, 0.3);
    padding: 10px;
    font-size: 16px;
    justify-items: space-between;
}

.view-image {
    margin-right: 10px;
}
.logo{
    width: 169.56px;
    Height:48px; 
    margin: 1em;
    
}

header{
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-bottom: 1px solid #E5E5E5;
    margin: 0px 50px 0px 50px ;
}

.slideshow-button{
    cursor: pointer;
    border: none;   
    outline:none;
    background-color: transparent;
    text-decoration: none;
    color: black;
}


.backdrop {

    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 20;
    background-color: rgba(0, 0, 0, 0.75);
  }
  
 
  .modal {
    position: fixed;   
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
    z-index: 101;
    
    -webkit-animation: slide-down 300ms ease-out forwards;
    
            animation: slide-down 300ms ease-out forwards;
  }
  


